import useWindowSize from "../../../hooks/useWindowSize";
import useValue from "../../../hooks/useValue";
import { Grid } from "@nextui-org/react";
import FooterInfoBlock from "./FooterInfoBlock";

const FooterInformation = ({blocks}) => {
    const windowSize = useWindowSize();
    const mobileVersion = windowSize.width < 1280;
    const currentExpanded = useValue("");

    const additionalHelpItems = [
        {text: "Персональные данные", link: "https://www.lipsum.com/feed/html"},
        {text: "Независимая оценка", link: "https://www.lipsum.com/feed/html"},
        {text: "Закупки", link: "https://www.lipsum.com/feed/html"},
    ]

    const onOpen = id => {
        if (currentExpanded.value === id) {
            currentExpanded.set("");
            return;
        }

        currentExpanded.set(id);
    }

    console.log(currentExpanded.value);
    return (
        <Grid.Container>
            <FooterInfoBlock
                title={"Контакты"}
                items={[
                    {text: "Телефон/Факс: 8 (861) 373-83-91"},
                    {text: "Бухгалтерия: 8 (861) 373-83-94"},
                    {text: "Служебный: 8 (861) 373-20-87"},
                    {text: "Касса: 8 (861) 373-20-40"},
                    {text: "Почта: arm_teatr@inbox.ru"},
                ]}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "contacts"}
                onOpen={() => onOpen("contacts")}
            />
            <FooterInfoBlock
                title={"Время работы"}
                items={[
                    {text: "ПН: выходной"},
                    {text: "ВТ-ВС: c 10:30 до 18:00"},
                    {text: "Перерыв: c 13:00 до 14:00"},
                ]}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "time"}
                onOpen={() => onOpen("time")}
            />
            <FooterInfoBlock
                title={"Адрес"}
                items={[
                    {text: "Краснодарский край"},
                    {text: "г. Армавир, ул. Ленина, д. 84"},
                ]}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "address"}
                onOpen={() => onOpen("address")}
            />
            <FooterInfoBlock
                title={"Помощь"}
                items={[
                    {text: "Финансовая деятельность", link: "https://www.lipsum.com/feed/html"},
                    {text: "Стажировка студентов инвалидов", link: "https://www.lipsum.com/feed/html"},
                    {text: "Противодействие коррупции", link: "https://www.lipsum.com/feed/html"},
                    {text: "Карточка учреждения", link: "https://www.lipsum.com/feed/html"},
                    {text: "Регламенты работы", link: "https://www.lipsum.com/feed/html"},
                ]}
                additionalItems={mobileVersion ? additionalHelpItems : null}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "help"}
                onOpen={() => onOpen("help")}
            />
            {!mobileVersion &&
                <FooterInfoBlock
                    title={""}
                    items={additionalHelpItems}
                    mobileVersion={mobileVersion}
                />
            }

        </Grid.Container>
    )
        ;
}

export default FooterInformation;